@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: left;
  background-color: black;
}





.posters{
    display: flex;
    object-fit: contain;
    overflow-y: hidden;
    padding: 20px;
    overflow-x: scroll;
    
}
.posters::-webkit-scrollbar{
    display: none;

}
.moiveposters{
    margin-right: 15px;
    width: 100%;
    max-height: 200px;
    transition: all 450ms;
    
    
}
.moiveposters:hover{
    -webkit-transform: scale(1.08);
            transform: scale(1.08);

}
h2{
    margin-left: 10px;
    color: white;
}

.Banner {
    color: white;
    object-fit: contain;
    height: 448px;
  }
  
  .banner_container {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
  }
  
  .banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
  }
  
  .banner_description {
    width: 45rem;
    line-height: 1.3;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
  }
  
  .banner_button {
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-right: 1rem;
    background-color: rgb(51, 51, 51, 0.5);
  
  }
  
  .banner_button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
  }
  
  .banner_fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);
  }

.topimg{
    width: 100px;
}
h4{
    color: white;
}
.nav-item:hover{
    border-bottom: 1px solid white;
    transition: 0.5s;
    -webkit-transform: scale(1.04);
            transform: scale(1.04);

}
.btn {
    position: absolute;
    top: 50%;
    bottom: 50%;
    right: 70%;
    left: 35%;
}

